@charset "UTF-8";

html,
body {
  background: #fff;
  color: #000;
}

body,
input,
select,
textarea,
button {
  font-family: "CenturyGothic", sans-serif;
  font-size: 100%;
  line-height: 1.2;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  width: 100%;
  height: auto;
}

.section-title {
  margin: 0;
  padding: 0;
  font-size: 40px;
  font-weight: normal;
  text-transform: uppercase;
}

.btn {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
}

.btn-danger {
  background-color: #fb3840;
  color: #fff;
}

.btn-danger:hover,
.btn-danger:focus,
.btn-danger:active {
  opacity: 0.8;
}

.btn-light {
  background-color: #fff;
  color: #000;
}

.btn-light:hover {
  opacity: 0.8;
}

.wrapper {
  max-width: 1280px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

.contacts {
  background-color: #fff;
}

.contacts .wrapper {
  padding-top: 10px;
  font-size: 0;
  text-align: center;
}

.contacts a {
  display: inline-block;
  font-size: 22px;
  color: #151515;
  text-decoration: none;
}

.contacts a:hover,
.contacts a:focus,
.contacts a:active {
  color: #fb3840;
}

.contacts a:not(:last-child) {
  margin-right: 80px;
}

.hero {
  background: url(../images/bkg-01.jpg) no-repeat center center;
  background-size: cover;
  height: 1130px;
}

.hero .wrapper {
  height: 1130px;
}

.hero-content {
  display: flex;
  flex-flow: column wrap;
  align-items: flex-end;
  width: 694px;
  height: 100%;
  margin-left: 42.5%;
  padding-right: 45px;
  background-color: rgba(255, 255, 255, 0.9);
}

.hero-logo {
  width: 417px;
  margin-top: 30px;
  margin-bottom: 112px;
  margin-right: 45px;
}

.hero-logo--1 {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  width: 300px;
}

.hero-logo--2 {
  display: block;
  margin-bottom: 10px;
}

.hero-logo p {
  margin: 0;
  padding: 0;
  font-size: 18px;
  color: #231f20;
}

.hero-heading {
  text-align: right;
  margin-bottom: 100px;
}

.hero-heading__title {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;
  font-size: 32px;
  font-weight: normal;
  text-transform: uppercase;
}

.hero-heading__desc {
  margin: 0;
  padding: 0;
  font-size: 24px;
}

.hero-price {
  width: 530px;
  padding: 40px 28px 30px 28px;
  border: 1.5px solid #242021;
  font-size: 0;
  text-align: center;
}

.price-title {
  margin-bottom: 16px;
  font-size: 23px;
  text-align: right;
}

.price-desc {
  margin: 0;
  margin-bottom: 28px;
  padding: 0;
  font-size: 20px;
  text-align: right;
}

.hero-price .btn {
  width: 200px;
}

.hero-price .btn:not(:last-child) {
  margin-right: 20px;
}

.benefits {
  min-height: 500px;
  padding-top: 90px;
  padding-bottom: 90px;
  background-color: #f6f6f6;
}

.benefits .section-title {
  margin-bottom: 60px;
  text-align: center;
}

.benefits-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: center;
}

.benefits-item {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(22.22222% - 20px);
  position: relative;
  padding-top: 100px;
  font-size: 18px;
  text-align: center;
}

.benefits-item::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 80px;
}

.benefits-item--cost::before {
  background: url(../images/ic-cost.svg) no-repeat center center;
  background-size: cover;
}

.benefits-item--discount::before {
  background: url(../images/ic-discount.svg) no-repeat center center;
  background-size: cover;
}

.benefits-item--time::before {
  background: url(../images/ic-time.svg) no-repeat center center;
  background-size: cover;
}

.benefits-item--delivery::before {
  background: url(../images/ic-delivery.svg) no-repeat center center;
  background-size: cover;
}

.conditions {
  min-height: 635px;
  padding-top: 85px;
  padding-bottom: 80px;
  background: #2d2741 url(../images/bkg-02.jpg) no-repeat center center;
  background-size: cover;
}

.conditions-container {
  width: 630px;
  margin-left: 90px;
}

.conditions .section-title {
  color: #fff;
  margin-bottom: 65px;
}

.conditions-form {
  width: 630px;
  padding: 30px 40px;
  border: 2px solid #fff;
  font-size: 0;
  text-align: center;
}

.form-title {
  margin-bottom: 32px;
  font-size: 24px;
  color: #fff;
}

.form-field__container {
  margin-bottom: 7px;
  border: 1.5px solid rgba(255, 255, 255, 0.87);
}

.form-field__container:last-child {
  margin-bottom: 0;
}

.form-field__container input {
  display: block;
  width: 100%;
  padding: 12px 28px;
  border: 0;
  font-size: 18px;
  text-align: left;
  color: #fff;
  background-color: transparent;
}

.form-field__container input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #fff;
}

.form-field__container input::-moz-placeholder {
  /* Firefox 19+ */
  color: #fff;
}

.form-field__container input:-ms-input-placeholder {
  /* IE 10+ */
  color: #fff;
}

.form-field__container input:-moz-placeholder {
  /* Firefox 18- */
  color: #fff;
}

.conditions-form .btn {
  margin-top: 20px;
  width: 270px;
  border: 0;
}

.reviews {
  padding-top: 90px;
  padding-bottom: 90px;
}

.reviews .section-title {
  margin-bottom: 60px;
  text-align: center;
}

.reviews-items {
  min-height: 400px;
}

.reviews-item {
  width: 450px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.reviews-item__img {
  width: 160px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.reviews-item__img img {
  border-radius: 50%;
}

.reviews-item__name {
  margin-bottom: 2px;
  font-size: 22px;
  font-weight: bold;
}

.reviews-item__shop {
  margin-bottom: 16px;
  font-size: 16px;
}

.reviews-item__review {
  font-size: 18px;
  line-height: 1.3;
}

.reviews-item__review::before {
  content: "«";
}

.reviews-item__review::after {
  content: "»";
}

.page-footer {
  padding-top: 65px;
  padding-bottom: 90px;
  background-color: #231f20;
}

.page-footer .wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  align-items: center;
}

.footer-logo {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(44.44444% - 20px);
  margin-left: calc(5.55556% + 10px);
}

.footer-logo__container {
  width: 200px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 12px;
}

.footer-contacts {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(38.88889% - 20px);
  margin-left: calc(11.11111% + 10px);
  padding-top: 12px;
  color: #fff;
}

.footer-contact {
  position: relative;
  display: block;
  margin-bottom: 26px;
  padding-left: 50px;
  font-size: 18px;
}

.footer-contact:last-child {
  margin-bottom: 0;
}

.footer-contact::before {
  position: absolute;
  content: "";
  display: inline-block;
  left: 0;
  top: 0;
}

.footer-contact a {
  color: #fff;
  text-decoration: none;
}

.footer-contact a:hover,
.footer-contact a:focus,
.footer-contact a:active {
  text-decoration: underline;
}

.contacts-phone::before {
  top: 1px;
  left: 2px;
  width: 23px;
  height: 36px;
  background: url(../images/ic-phone.svg) no-repeat center center;
  background-size: cover;
}

.contacts-email::before {
  width: 30px;
  height: 22px;
  background: url(../images/ic-email.svg) no-repeat center center;
  background-size: cover;
}

.contacts-address::before {
  top: 1px;
  left: 2px;
  width: 24px;
  height: 32px;
  background: url(../images/ic-address.svg) no-repeat center center;
  background-size: cover;
}

.socials {
  padding-top: 16px;
  padding-bottom: 16px;
  background-color: #fff;
  font-size: 0;
  text-align: center;
}

.socials-link {
  display: inline-block;
  width: 26px;
  height: 26px;
  margin-right: 20px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
}

.socials-link:last-child {
  margin-right: 0;
}

.socials-link:hover,
.socials-link:focus,
.socials-link:active {
  transform: scale(1.2, 1.2);
}

.ic-fbook {
  background-image: url(../images/social-facebook.svg);
}

.ic-instagram {
  background-image: url(../images/social-instagram.svg);
}

.ic-vk {
  background-image: url(../images/social-vk.svg);
}

.ic-twitter {
  background-image: url(../images/social-twitter.svg);
}

.ic-gplus {
  background-image: url(../images/social-googleplus.svg);
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .benefits-item {
    width: calc(44.44444% - 20px);
  }

  .benefits-item {
    margin-bottom: 40px;
  }

  .benefits-item:last-child {
    margin-bottom: 0;
  }

  .footer-logo {
    width: calc(100% - 20px);
  }

  .footer-logo {
    margin-left: calc(0% + 10px);
    margin-bottom: 30px;
  }

  .footer-contacts {
    width: calc(100% - 20px);
  }

  .footer-contacts {
    margin-left: calc(0% + 10px);
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .benefits-item {
    width: calc(55.55556% - 20px);
  }
}

@media screen and (max-width: 544px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 420px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}